import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ApiModule as ApiModuleHr } from 'src/app/sdk/hr/api.module';
import { ApiModule as ApiModuleUser, ApiModule } from 'src/app/sdk/user/api.module';
import { ApiModule as ApiModulePurchasing } from 'src/app/sdk/purchasing/api.module';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { WidgetsComponentsModule } from '../widgets/widgets-components.module';
import { NzLayoutModule, NzPopconfirmModule, NgZorroAntdModule } from 'ng-zorro-antd';
import { LayoutModule } from '../layout/layout.module';
import { SearchItemComponent } from './search-item/search-item.component';
import { FormsModule } from '@angular/forms';
import { SearchAssetComponent } from './search-asset/search-asset.component';

const COMPONEN = [
    SearchItemComponent, SearchAssetComponent
]

@NgModule({
    declarations: [...COMPONEN],
    imports: [
        CommonModule, CommonModule,
        ApiModule.forRoot({ rootUrl: 'http://erp-asset.vm.devetek.com/api' }),
        ApiModuleHr.forRoot({ rootUrl: 'http://erp-hr.vm.devetek.com/api' }),
        ApiModulePurchasing.forRoot({ rootUrl: 'http://erp-purchasing.vm.devetek.com/api' }),
        ApiModuleUser.forRoot({ rootUrl: 'http://erp-user.vm.devetek.com/api' }),
        PerfectScrollbarModule,
        WidgetsComponentsModule,
        NzLayoutModule,
        FormsModule,
        NzPopconfirmModule,
        LayoutModule,
        NgZorroAntdModule,
    ],
    exports: [...COMPONEN],
    entryComponents: [...COMPONEN]
})
export class SearchItemModule { }
