import { Component, OnInit, Input } from '@angular/core';
import { NzDrawerRef } from 'ng-zorro-antd';
import { AssetModelxService, AssetPartIdService } from 'src/app/sdk/asset/services';
import { Part_id } from 'src/app/sdk/asset/models';

@Component({
    selector: 'erp-search-item',
    templateUrl: './search-item.component.html',
    styleUrls: ['./search-item.component.scss']
})
export class SearchItemComponent implements OnInit {

    @Input() value = '';

    constructor(
        private drawerRef: NzDrawerRef<string>,
        private assetModelxService: AssetModelxService,
        private assetPartIdService: AssetPartIdService
    ) { }

    ngOnInit() {
        this.searchData()
        this.getPartId()
    }

    listPartId: Part_id[] = [];
    getPartId() {
        this.assetPartIdService.getAssetPartIds().subscribe(
            data => {
                this.listPartId = data.data;
            }
        )
    }

    close(): void {
        this.drawerRef.close(this.value);
    }

    pageIndex = 1
    pageSize = 10
    total = 1
    listOfData: any[] = []
    loading = true
    sortValue: string | null = null
    sortKey: string | null = null
    search: string = ''
    id_part_id: string = null

    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key
        this.sortValue = sort.value
        this.searchData()
    }

    searchData(reset: boolean = false): void {
        if (reset) {
            this.pageIndex = 1
        }

        let param = {
            pageIndex: this.pageIndex,
            pageSize: this.pageSize,
            sortKey: this.sortKey,
            sortValue: this.sortValue,
            search: this.search,
            id_part_id: this.id_part_id,
        };
        if (!this.id_part_id) {
            delete param.id_part_id;
        }
        this.loading = true
        this.assetModelxService
            .getAssetModelxes(
                JSON.stringify(param),
            )
            .subscribe((data: any) => {
                this.loading = false
                this.total = data.data.total
                this.listOfData = data.data.data

            })
    }

    updateFilter(value: string): void {
        this.search = value
        this.searchData(true)
    }

    selectModel(id_model: string) {
        this.value = id_model;
        this.close();
    }


}
